<template>
    <div>
      <div class="row">
        <div class="col">
          <h3 class="fw-bold">State</h3>
        </div>
        <div class="col-md-1 col-sm-6 my-2">
          <router-link to="/admin/state">
            <v-btn color="error">
              <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>
              List
            </v-btn>
          </router-link>
        </div>
      </div>
      <template>
        <div class="card">
          <div class="card-body my-2">
            <v-form ref="form" v-model="valid">
              <v-select :items="country" label="Parent Country" prepend-inner-icon="mdi-home-city"></v-select>
              <v-text-field label="State Name" v-model="stateform.statename" :rules="statename"></v-text-field>
              <div class="my-2">
                <v-btn color="success" class="mr-4" :disabled="!valid" @click="addstate">SAVE</v-btn>
                <v-btn color="error" class="mr-4" @click="reset">CANCEL</v-btn>
              </div>
            </v-form>
          </div>
        </div>
      </template>
      <v-snackbar v-model="snackbar" :timeout="timeout" type="success" border="left" elevation="3">
        <template v-slot:action="{ attrs }">
          <v-btn color="blue"  v-bind="attrs" text @click="snackbar = false">Close</v-btn>
        </template>
        {{text}}
      </v-snackbar>
    </div>
  </template>
  
  <script>
  import axios from "axios";

  export default {
    name: "CreateState",
    components: {  },
    data: () => ({
      country: ['India'],
      switch1: false,
      stateform:{
        statename: '',
      },
      valid: false,
      snackbar: false,
      text: '',
      timeout: '',
      show1: false,
      statename: [
        v => !!v || 'State Name is required.',
      ],
    }),
  
    methods: {
      reset(){
        this.$refs.form.reset();
      },

      addstate() {
        const config = {
          headers: { 'content-type': 'multipart/form-data' }
        }
        let data = {
          countryId:  1,
          statename:  this.stateform.statename,
          state_status: 1,
        };
        axios.post("add/state",data,config)
            .then((resp) => {
              if(resp.data.message) {
                this.snackbar = true;
                this.text = resp.data.message;
                this.timeout = 3000;
                this.$refs.form.reset();
              }
            })
            .catch(error => {
              // Handle the error
              var data = error.toJSON();
              if(data.status == 400){
                this.snackbar = true;
                this.text = "something went wrong.";
                this.timeout = 3000;
              }
            });
        this.$refs.form.validate();
      },
    },
  };
  </script>
  